body, html {
  padding:0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Navigation {
  display: flex;
  width:100%;
  justify-content: stretch;
  background: #eee;
  padding:1em;
  color: orange;
  font-size:2em;
  align-items: stretch;
  box-sizing: border-box;

}

.Navigation * {
  text-decoration: none;
  color: orange;
}

.banner {
  width: 100%;
  display: flex;
  justify-content: stretch;
  align-content: center;
  background: orange;
  color:#eee;
  font-weight: 700;
  padding: 3em;
  box-sizing: border-box;
  flex-flow: row wrap;
  margin:0;
}

.banner > * {
  display: block;
  box-sizing: border-box;
  min-width: 400px;
  margin: auto;
  padding: 1em;
}

.banner .tagline {
  width: 50%;
}

.banner p {
  display: block;
  width: 100%;
  font-size: 3em;
  box-sizing: border-box;
  margin: 0;
}

.banner .copic {
  width: 50%;
  box-sizing: border-box;
}

.copic img {
  width: 100%;
  border-radius: 10px;
  box-shadow: brown 0 0 100px 10px;
}

.videos {
  margin:0;
  padding:2em;
  color: orange;
  background: black;
}

iframe {
  margin: 0.5em;
}

.grid {
  width:100%;
  display:flex;
  flex-direction: row;
  justify-content: center;
  flex-flow: row wrap;
}

.card {
  display: inline-flex;
  flex-direction: column;
  background-repeat: no-repeat;
  width: 400px;
  border-radius: 10px;
  margin: 0.5em;
  overflow: hidden;
}

.card .meta {
  width: 100%;
  display: block;
  background: burlywood;
  padding: 0 1em 1em 1em;
}


.card * {
  min-width: 200px;
  max-width: 400px;
}

.info .meta * {
  margin-right: 0.5em;
}

.card .title {
  font-weight: 600;
  display: inline-block;
  height: 1.25em;
  overflow: show;
}

.card .image {
  display:block;
  overflow: hidden;
  width: 100%;
  border-bottom: solid 2px orange;
  padding: 0;
  margin: 0;
}


.card .source {
  display:inline-block;
  position:relative;
  text-align:center;
  height: 1.5em;
  padding: 0.2em;
  left: 2em;
  top: -1em;
  background: orange;
  border-radius: 5px;
}

.card:hover .image img {
  transform: scale(1.5)
}

.card .image img {
  position:relative;
  width: 100%;
  transition: all 0.1s ease-in;
  z-index: -99;
}


.macros {
  display: flex;
}

.macros > * {
  display:inline-block;
  color: white;
}

.macros .fat {
  background: black;
}


.macros .protein {
  background: red;
}

.macros .carbs {
  background:green;
}

.Nutrition {
  display: flex;
  background: orange;
  justify-content: space-around;
  flex-flow: row wrap;
}

.Nutrition > div {
  display:flex;
  flex-direction:column;
  align-items: center;
  padding: 2em;
  flex-grow: 1;
}

.header {
  display: flex;
  max-width: 1200px;
  flex-flow: row wrap;
  justify-content: space-around;
}

.center{
  display: flex;
  flex-flow: row wrap;
  justify-items: stretch;
  align-items: flex-start;
  padding: 1em;
}

.steps, .ingredients {
  min-width: 400px;
  max-width: 800px;
}

.header img {
  border-radius: 15px;
}

.tag {
  white-space: nowrap;
  border: blue solid 1px;
  padding: 0.5em;
  text-decoration: none;
  line-height: 1em;
  margin: 0.2em;
  border-radius: 10px;
  font-size: 0.8em;
}

.flex {
  display: inline-flex;
  flex-flow: row wrap;
  align-items: center;
}

.summaryList li {
  line-height: 2em;
}


.controlsWithCaption {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  flex: 1;
  flex-flow: row wrap;
}

.controlsWithCaption > * {
  flex-grow: 1;
}

.search {
  display: inline-block;
  width: 100%;
  min-width: 200px;
  max-width: 500px;
}

.controlsWithCaption h2 {
  margin-right: 1em;
}

.controls {
  display: flex;
  align-items: center;
}

.Pagination {
  display:flex;
  flex-direction: row nowrap;
  justify-content: flex-end;
  align-items: center;
}


.btn {
  border: solid 1px lightblue;
  background: white;
  margin: 0.2em;
  padding: 0.7em;
  border-radius: 5px;
  text-decoration: none;
  color: black;
}

.btn:hover {
  background: lightblue;
}

.disabled {
  background: #eee;
}

.disabled:hover {
  background: #ddd;
}

.section {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 1800px;
}

.section > .Pagination {
  align-self: flex-end;
  max-width: 400px;
}


.ads {
  color: white;
  display:flex;
  width: 100% !important;
  max-width: 100% !important;
  background:green;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
  padding-top: 2em;
}

.ads h1 {
  margin-bottom: 3em;
}

.footer {
  background:black;
  width: 100%;
  height: 20em;
}